"use client";

import { useEffect, useRef } from "react";
import { useScroll, useTransform, motion } from "framer-motion";

export function AnimatedBackground() {
  const canvasRef = useRef<HTMLCanvasElement>(null);

  // scroll → factor para animar luces
  const { scrollYProgress } = useScroll();
  const scrollFactor = useTransform(scrollYProgress, [0, 1], [0, 800]);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext("2d")!;
    let w = (canvas.width = window.innerWidth);
    let h = (canvas.height = window.innerHeight);

    // luces iniciales
    const lights = Array.from({ length: 12 }).map(() => ({
      x: Math.random() * w,
      y: Math.random() * h,
      r: 80 + Math.random() * 140,
      dx: (Math.random() - 0.5) * 0.3,
      dy: (Math.random() - 0.5) * 0.3,
    }));

    const green = "rgba(101, 207, 114, 0.25)"; // verde marca con glow

    function animate() {
      ctx.clearRect(0, 0, w, h);

      ctx.globalCompositeOperation = "lighter"; // Glow neon

      lights.forEach((l) => {
        // movimiento orgánico suave
        l.x += l.dx;
        l.y += l.dy;

        // rebote suave
        if (l.x < 0 || l.x > w) l.dx *= -1;
        if (l.y < 0 || l.y > h) l.dy *= -1;

        const gradient = ctx.createRadialGradient(
          l.x,
          l.y,
          0,
          l.x,
          l.y,
          l.r
        );

        gradient.addColorStop(0, green);
        gradient.addColorStop(1, "transparent");

        ctx.fillStyle = gradient;
        ctx.beginPath();
        ctx.arc(l.x, l.y, l.r, 0, Math.PI * 2);
        ctx.fill();
      });

      requestAnimationFrame(animate);
    }

    animate();

    const resize = () => {
      w = canvas.width = window.innerWidth;
      h = canvas.height = window.innerHeight;
    };

    window.addEventListener("resize", resize);
    return () => window.removeEventListener("resize", resize);
  }, []);

  return (
    <motion.canvas
      ref={canvasRef}
      className="fixed inset-0 w-full h-full -z-10 pointer-events-none"
      style={{
        y: scrollFactor, // movimiento vertical sutil con scroll
      }}
    />
  );
}
